:root {
  /* Color */
  --color-background: #f1e7dc;
  --color-background-dark: #e6dcd2;
  --color-text: #1a202b;
  --color-author-bg: #f0f8ff70;
  --color-author-bg-hover: #f0f8fff0;
  --color-navbar-bg: #e4d0baa0;
  --color-dark-mode-btn-bg: #313131;
  --color-dark-mode-btn-bg-hover: #111111;
  --color-toggle-btn-bg-hover: #edf2f7a0;
  --color-accent: #36d7b7;
  --color-accent-light: #b4fff0;
  --color-anchor: #89b1f4;

  /* Time */
  --animation-duration: 150ms;
  --animation-slow-duration: 250ms;

  /* shadow */
  --shadow-work: 1px 1px 2rem rgba(0, 0, 0, 0.3);

  /* Size */
  --size-sm: 480px;
  --size-md: 768px;
  --size-lg: 1024px;
  --size-xl: 1280px;
  --size-xxl: 1536px;

  /* Font Size */
  --font-size-xxl: 2.5rem;
  --font-size-xl: 2rem;
  --font-size-lg: 1.5rem;
  --font-size-md: 1rem;
  --font-size-sm: 0.8rem;
}

html.dark {
  /* Color */
  --color-background: #212121;
  --color-background-dark: #1a1a1a;
  --color-text: #ededee;
  --color-author-bg: #31313470;
  --color-author-bg-hover: #313134f0;
  --color-navbar-bg: #3f3f3fa0;
  --color-dark-mode-btn-bg: #fdba74;
  --color-dark-mode-btn-bg-hover: #fc9630;
  --color-toggle-btn-bg-hover: #3f668d30;
  --color-accent-light: #36d7b7;

  /* shadow */
  --shadow-work: 1px 1px 2rem rgba(120, 120, 120, 0.3);
}

@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&family=Work+Sans&display=swap');
body {
  margin: 0;
  font-family: 'Nanum Gothic', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  background-color: var(--color-background);
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

ul {
  list-style: none;
  padding-left: 0;
}

li {
  list-style: none;
}

button {
  outline: none;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

a {
  text-decoration: none;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  :root {
    /* Font Size */
    --font-size-xxl: 2rem;
    --font-size-xl: 1.7rem;
    --font-size-lg: 1.3rem;
    --font-size-md: 0.8rem;
    --font-size-sm: 0.6rem;
  }
}

@media screen and (max-width: 640px) {
  :root {
    /* Font Size */
    --font-size-xxl: 1.8rem;
    --font-size-xl: 1.3rem;
    --font-size-lg: 1rem;
    --font-size-md: 0.6rem;
    --font-size-sm: 0.4rem;
  }
}
