.container {
  width: 90vw;
  max-width: 850px;
  margin: 0 auto;
  text-align: center;
  color: var(--color-text);
  transition: all var(--animation-slow-duration) ease;
}

.title {
  display: flex;
  flex-direction: column;
  width: 190px;
  margin-left: 5rem;
}

.title::after {
  content: '';
  width: 100%;
  margin-top: 0.5rem;
  border: 2px solid var(--color-accent);
}

@media screen and (max-width: 480px) {
  .title {
    margin-top: 20%;
    margin-left: 5%;
  }
}
