.wrapper-container {
  display: inline-block;
  z-index: 1;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vw;
  max-width: 350px;
  margin: 2rem 1rem;
  cursor: pointer;
  transition: all var(--animation-duration) ease;
}

.image-container {
  width: 100%;
  max-height: 200px;
  border-radius: 10px 10px 0 0;
  z-index: 1;
  overflow: hidden;
  box-shadow: var(--shadow-work);
}

.image {
  width: 100%;
}

.container:hover {
  transform: scale(1.02);
}

.text-container {
  width: 100%;
  padding: 1rem;
  text-align: left;
  border-radius: 0 0 10px 10px;
  background-color: var(--color-author-bg);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: var(--shadow-work);
}

.type {
  display: inline-block;
  margin: 0 0 1rem 0;
  padding: 0.5rem;
  color: white;
  border-radius: 10px;
  font-weight: bold;
}

.title {
  margin: 0 0 1rem 0;
}

.date {
  font-weight: bold;
  margin-top: 1rem;
  opacity: 0.6;
}

@media screen and (max-width: 480px) {
  .text-container {
    padding: 0.5rem 1rem;
  }

  .type {
    font-size: 0.8rem;
    margin: 0 0 0.5rem 0;
  }

  .title {
    margin: 0 0 0.5rem 0;
  }

  .summary {
    font-size: 0.8rem;
  }

  .date {
    margin-top: 0.5rem;
  }

  .image-container {
    max-height: 170px;
  }
}
