.background-container {
  position: fixed;
  width: 100vw;
  top: 0;
  z-index: 10;
  background-color: transparent;
}

.background-container.accent {
  background-color: var(--color-navbar-bg);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

.main-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 750px;
  margin: auto;
}

.title-container {
  display: flex;
  align-items: center;
}

.title {
  display: flex;
  align-items: center;
  margin: 0 1rem 0 0.5rem;
  color: var(--color-text);
  font-size: 1.3rem;
  font-weight: bold;
}

.title:hover .title-icon,
.title .title-icon:hover {
  transform: rotate(0);
}

.title-icon {
  font-size: 1.5rem;
  transform: rotate(-30deg);
  margin: 0 0.5rem;
  transition: all var(--animation-duration) ease;
}

.menu__container {
  display: flex;
}

.menus {
  display: flex;
  align-items: center;
  margin: 0;
}

.toggle-button-wrapper {
  position: relative;
  display: none;
  width: 48px;
  height: 48px;
  margin: 0 1rem;
}

@media screen and (max-width: 768px) {
  .main-container {
    flex-direction: column;
    width: 100vw;
  }

  .left-side {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }

  .title-container {
    width: 100%;
    flex: 1 0 0;
  }

  .title {
    margin-right: 0.5rem;
    font-size: 1.1rem;
  }

  .menus {
    flex-direction: column;
    display: none;
  }

  .menus.open {
    display: block;
  }

  .menu {
    display: none;
    padding: 0.4rem 0;
  }

  .menu.open {
    display: block;
  }

  .toggle-button-wrapper {
    display: block;
  }

  .toggle-button {
    position: absolute;
    top: 50%;
    left: 0;
    font-size: 2.5rem;
    color: var(--color-text);
    opacity: 0;
    transform: translateY(-50%);
    transition: all var(--animation-duration) ease-out;
  }

  .toggle-button.show {
    opacity: 1;
    z-index: 1;
  }

  .toggle-button.none {
    opacity: 0;
  }

  .toggle-button:hover {
    background-color: var(--color-toggle-btn-bg-hover);
    border-radius: 5px;
  }
}
