a {
  color: var(--color-anchor);
}

.wrapper-container {
  transition: all var(--animation-slow-duration) ease;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90vw;
  max-width: 850px;
  margin: 1rem auto;
  font-weight: bold;
  color: var(--color-text);
  transition: all var(--animation-slow-duration) ease;
}

.title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-anchor);
  font-size: 1rem;
  font-weight: bold;
}

.link::after {
  content: '';
  width: 100%;
  margin-top: 0.2rem;
  border: 1px solid var(--color-anchor);
  opacity: 0;
  transition: all var(--animation-duration) ease;
}

.link:hover.link::after {
  opacity: 1;
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 3rem;
  font-size: 2rem;
}

.title::after {
  content: '';
  width: 100%;
  margin-top: 0.2rem;
  border: 2px solid var(--color-accent-light);
}

.date {
  padding: 0.5rem;
  border-radius: 5px;
  background-color: var(--color-author-bg);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  color: var(--color-text);
}

.content-container {
  width: 90%;
  max-width: 600px;
  margin: 1rem auto;
}

.specification-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.specification-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 450px;
  height: 50px;
  margin: 0.2rem;
  font-size: 0.9rem;
  color: #1a202b;
}

.specification {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  height: 100%;
  padding: 0 1rem;
  background-color: var(--color-accent-light);
  border-radius: 10px 0 0 10px;
  font-size: 1rem;
}

.specification-content {
  display: block;
  text-align: center;
  width: 75%;
  height: 100%;
  padding: 0.95rem 0.5rem;
  background-color: aliceblue;
  border-radius: 0 10px 10px 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.specification-content .anchor {
  color: black;
}

.image-container {
  text-align: center;
}

.image {
  width: 90vw;
  max-width: 600px;
  margin: 0.5rem;
  border-radius: 10px;
  box-shadow: var(--shadow-work);
}

@media screen and (max-width: 480px) {
  .container {
    top: 10%;
  }

  .link {
    font-size: 0.8rem;
  }

  .title {
    margin: 0 1rem;
    font-size: 1.5rem;
  }

  .date {
    font-size: 0.8rem;
  }

  .content {
    font-size: 0.8rem;
  }

  .specification-container {
    width: 300px;
  }

  .specification {
    font-size: 0.8rem;
  }

  .specification-content {
    font-size: 0.7rem;
    padding: 1.2rem 0.5rem;
  }

  .image {
    width: 100vw;
    border-radius: 0;
  }
}
