.background {
  display: flex;
  align-items: center;
  width: 60px;
  height: 36px;
  border-radius: 18px;
  background-color: var(--color-dark-mode-btn-bg);
  transition: all var(--animation-duration) ease;
  pointer-events: none;
}

.background:hover {
  background-color: var(--color-dark-mode-btn-bg-hover);
}

.button-wrapper {
  width: 30px;
  height: 30px;
  background-color: white;
  border-radius: 100%;
  transform: translateX(10%);
  transition: all var(--animation-duration) ease;
}

.button-wrapper.selected {
  transform: translateX(90%);
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0;
  color: var(--color-dark-mode-btn-bg);
  transition: all var(--animation-duration) ease;
  pointer-events: auto;
}

.icon {
  font-size: 1rem;
}

@media screen and (max-width: 768px) {
  .background {
    width: 32px;
    height: 32px;
  }

  .button-wrapper {
    width: 26px;
    height: 26px;
    transform: translateX(11%);
  }

  .button-wrapper.selected {
    transform: translateX(11%);
  }
}
