.background-container {
  background-color: var(--color-background-dark);
}

.container {
  position: relative;
  width: 640px;
  height: 640px;
  margin: auto;
  margin-top: -120px;
  margin-bottom: -200px;
}

.author {
  position: absolute;
  display: inline-block;
  right: 15%;
  top: 60%;
  padding: 0.8rem;
  border-radius: 5px;
  background-color: var(--color-author-bg);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  font-size: 0.7rem;
  color: var(--color-text);
}

.author:hover {
  background-color: var(--color-author-bg-hover);
}

@media screen and (max-width: 768px) {
  .container {
    width: 480px;
    height: 480px;
    margin-top: -6px;
    margin-bottom: -140px;
  }

  .author {
    padding: 0.8rem;
    font-size: 0.6rem;
  }
}

@media screen and (max-width: 480px) {
  .container {
    width: 280px;
    height: 280px;
    margin-top: -20px;
    margin-bottom: -40px;
  }

  .author {
    top: 50%;
    right: -5%;
    padding: 0.6rem;
    font-size: 0.5rem;
  }
}
