.main-container {
  margin: 0 0.3rem;
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 56px;
  margin: 0 0.5rem;
  text-transform: capitalize;
  color: var(--color-text);
  font-size: 1rem;
  font-weight: bold;
  opacity: 0.9;
}

.title:hover {
  color: var(--color-accent);
}

.title.selected::after {
  content: '';
  width: 110%;
  margin-top: 0.5rem;
  border: 1px solid var(--color-accent);
}
