.container {
  transition: all var(--animation-slow-duration) ease;
}

.title {
  margin-top: 3rem;
  text-align: center;
  font-size: 3rem;
  color: var(--color-text);
  z-index: 1;
}

.puffloader-container {
  position: relative;
  left: 50%;
  top: 1rem;
  transform: translateX(-50%);
}

.puffloader-arrow {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 1.5rem;
  color: var(--color-accent);
  transform: translate(-50%, -70%);
}

.about-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-text);
}

.offset {
  height: 50px;
}

.greetings {
  width: 90vw;
  max-width: 600px;
  padding: 1rem;
  text-align: center;
  border-radius: 10px;
  font-size: 1rem;
  background-color: var(--color-author-bg);
}

.name-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90vw;
  max-width: 600px;
  padding: 1rem;
}

.name {
  font-size: 3.5rem;
}

.skill-container {
  display: flex;
  margin: 0.5rem 0;
}

.skill {
  display: inline-block;
  margin: 0 0.3rem;
}

.avatar {
  border-radius: 50%;
  border: 2px solid white;
  width: 150px;
  height: 150px;
  transform: translateY(-0.5rem);
}

.introduce {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90vw;
  max-width: 700px;
  margin: 1rem 0;
  gap: 0.5rem;
  transition: all 600ms ease;
}

.subtitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100px;
}

.subtitle::after {
  content: '';
  width: 100%;
  margin-top: 0.5rem;
  border: 2px solid var(--color-accent);
  transform: translateX(-5px);
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90vw;
  max-width: 700px;
  margin: 1rem 0;
  transition: all 600ms ease;
}

.profile-item {
  display: flex;
  margin: 0.4rem 0;
}

.profile-date {
  font-weight: bold;
  margin-right: 1rem;
}

.progress-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .title {
    margin-top: 0.5rem;
    font-size: 2rem;
  }

  .offset {
    height: 40px;
  }

  .name-container {
    flex-direction: column;
  }

  .avatar {
    transform: translateY(0);
  }

  .introduce {
    padding: 0 1rem;
  }

  .profile {
    padding: 0 1rem;
  }
}

@media screen and (max-width: 480px) {
  .avatar-container {
    position: absolute;
    top: 170px;
    left: 50%;
    transform: translateX(-50%);
  }

  .title {
    width: 100vw;
    margin-top: 0;
    font-size: 1.2rem;
  }

  .puffloader-container {
    top: 0;
  }

  .name {
    font-size: 2.5rem;
  }

  .skill-container {
    flex-direction: column;
  }

  .profile-item {
    flex-direction: column;
    gap: 0.2rem;
  }
}
