h2 {
  margin: 0;
}

p {
  margin: 0;
}

.container {
  /* Size */
  --size-text: 0.75rem;
  /* Styles */
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.5rem;
  background-color: var(--color-navbar-bg);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

.author {
  margin: 0 2rem;
  font-size: var(--size-text);
  font-weight: bold;
  color: var(--color-text);
}

.info-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.email-container,
.channel-container {
  margin: 0 2rem;
  font-size: var(--size-text);
  font-weight: bold;
  color: var(--color-text);
}

.email {
  color: var(--color-text);
  transition: all var(--animation-duration) ease;
}

.email:hover {
  color: var(--color-accent);
}

.channel {
  margin: 0 0.2rem;
  font-size: 2rem;
  color: var(--color-text);
  transition: all var(--animation-duration) ease;
}

.channel:hover {
  color: var(--color-accent);
}

@media screen and (max-width: 1024px) {
  .container {
    flex-direction: column;
  }

  .author {
    margin-top: auto;
    margin-bottom: 0.5rem;
    padding-left: 0.5rem;
  }
}

@media screen and (max-width: 768px) {
  .info-container {
    flex-direction: column;
  }

  .email-container {
    margin: 0.4rem auto 0.4rem 0;
  }

  .channel-container {
    margin: 0.4rem auto 0 0;
  }

  .author {
    padding-left: 0;
    transform: translateX(-8px);
  }
}
